h1{
  text-align: center;
  font-size: 40px !important;
  font-family:  Century Schoolbook, TeX Gyre Schola, serif;
  text-shadow: 1px 1px rgb(167, 166, 166);
}
body{
  background-image:url(wave.mp4) ;
}
.heading-container{
  position: relative;
}
.filter-container:not(.form-group) { 
  opacity: 0.89; 
} 
.graph-container{
  opacity: 0.89;
}
.table-container{
  opacity: 0.89;
}

.form-group{
  padding-top: 22px;
  font-family:'New Century Schoolbook, TeX Gyre Schola, serif';
  color: rgb(0, 35, 80);
  font-weight: bold;
  
}
.form-group>.form-control{
  opacity: 0.85;
  box-shadow: 1px 2px 20px 7px #888888;
}
.btn-secondary{
  background-color: rgb(0, 35, 80) !important;
}
video{
  
  object-fit: cover;
  width: 100vw;
  height: 100vh;
  position: fixed;
  top: 0;
  left: 0;

}
.heading-container{
  display: grid;
    grid-template-rows: 60px;
    color: white;
    align-items: center;

    justify-items: center;
    
    margin: 20px;
    
}
.pageheading{
  font-size: 40px;
  font-weight: 700;
  text-shadow: 1px 1px rgb(167, 166, 166);
  font-family: "Times New Roman", Times, serif;

}
.hidden-table-container{
  display: none;
}
.input-container{
  position: relative;
  display: grid;
  grid-template-rows: 170px;
  grid-template-columns: repeat(1, 1fr);
  color: black;
  /* align-items: center; */

  /* justify-items: center; */
  font-size: 20;
  margin-top: 20px;
  /* width: 600px; */
  padding-right: 50px;
  padding-left: 50px;
  margin-left: 200px;
  margin-right: 200px;
  

}
.graph-container{
  position: relative;
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  grid-template-rows: 400px;
  color: black;
  background-color: white;
  border-radius: 25px;

  /* background-color: rgb(236, 233, 233); */
  justify-items: center;
  font-size: 20;
  
  margin-left: 100px;
  margin-right: 100px;
  margin-bottom: 30px;
  box-shadow: 1px 2px 9px 7px #888888;
  overflow: auto;
  

}
.filter-container{
  position: relative;
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  grid-template-rows: 200px;
  color: black;
  background-color: white;
  border-radius: 25px;

  /* background-color: rgb(236, 233, 233); */
  justify-items: center;
  font-size: 20;
  margin-top: 10px;
  margin-left: 100px;
  margin-right: 100px;
  margin-bottom: 30px;
  box-shadow: 1px 2px 9px 7px #888888;
  overflow: auto;
}
#select option { width: 50px; }
.table-container{
  
  position: relative;
  overflow: auto;
    display: grid;
    border-radius: 25px;
      grid-template-columns: repeat(1, 1fr);
      grid-template-rows: auto;
      color: black;
      background-color: white;
  
      /* background-color: rgb(236, 233, 233); */
      justify-items: center;
      font-size: 20;
     
      box-shadow: 1px 2px 9px 7px #888888;
      margin-left: 100px;
      margin-right: 100px;
      margin-bottom: 100px;
  
}
.footer-container{
  width: 100%;
  left: 0;
    bottom: 0;
}
.grid-item{
  display: grid;
  margin: 10px;
  background-color: rgb(248, 246, 246);
}

@media(max-width:500px){
  .heading-container{
    margin-top: 30px;
  }
 
  .input-container{
    margin-left: 30px;
    margin-right: 30px;
    margin-bottom: 0;
  }
  .table-container{
    margin-right: 50px;
    margin-left: 50px;
  }
  .filter-container{
    margin-right: 50px;
    margin-left: 50px;
    margin-top: 0;
  }
  .graph-container{
    margin-right: 50px;
    margin-left: 50px;
  }
  option {
    display: inline-block;
  }

}
@media(max-width:769px) and (min-width: 500px){
  .input-container{
    margin-left: 40px;
    margin-right: 40px;
  }

}
