body {
    background: #eee
}
div.container.footer {
    position: fixed;
    left: 0;
    bottom: 0;
    max-width: 100%;
    padding: 0;
    
 }


.news-scroll a {
    text-decoration: none;
    color: white;
    font-weight: 600;

}
.news-scroll{
    padding-top: 5px;
}
#ticker{
    color: rgb(0, 35, 80);
    background-color: white;
    width: 90px;
    justify-content: center;
    align-items: center;
    font-size: 20px;
}
.d-flex.breaking-news{
    background-color: rgb(0, 35, 80);
    height: 40px;
    color: white;
    font-weight: 900;
    /* justify-content: center;
    align-items: center; */
}
.hidden-footer-container{
    display: none;
}
